import axios from 'axios';

const adaptSelectedOption = (selectedOption) => {
  const mapping = {
    '-': '-',
    '': 'NOUN',
    '': 'VERB',
    ' ': 'AUX',
    '': 'ADJ',
    '': 'PRON',
    '': 'NUM',
    ' ': 'ANUM',
    ' ': 'PROPN',
    '': 'DET',
    '': 'PART',
    '': 'ADV',
    ' ': 'ADVPRO',
    '': 'ADP',
    '': 'SYM',
    ' ': 'CCONJ',
    ' ': 'SCONJ',
    '': 'INTJ',
    '': 'PRED',
    ' ': 'PARENTH',
    'COM': 'COM',
    ' ': 'PUNCT'
  };

  return mapping[selectedOption] || '-'; //  selectedOption  ,  'X'    
};

export const fetchDataFromAPI = async (mode, selectedOption, inputValue, token) => {
  try {
    if (mode === 1) {
      selectedOption = adaptSelectedOption(selectedOption);
    }
    if (mode === 2) {
      if (selectedOption === " ") {
        selectedOption = "All";
      }
    }

    const response = await axios.post('https://lemmaapp.ru/lemmatize', {
      mode: mode,
      selected_option: selectedOption,
      input_value: inputValue,
    });

    const result = response.data.lemma; //   
    console.log(token)
    if (token) {
      handleHisoryPost(inputValue, selectedOption, mode, result,token);
    }
    return result;
  } catch (error) {
    console.error('  :', error);
    throw new Error('  ');
  }
};


const handleHisoryPost = async (inputValue, selectedOption, mode, result, token) => {
  try {
    console.log(token);
    var start_word = inputValue;
    if (mode === 2) {
      start_word = selectedOption === "All" ? inputValue : selectedOption;
    }

    const response = await fetch("https://lemmaapp.ru/server1/history", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`, //    
      },
      body: JSON.stringify({
        word: start_word,
        lemmatize_type: mode,
        result: result,
        selected_option: selectedOption,
      }),
    });

    if (!response.ok) {
      const data = await response.json();
      console.error("  :", data.detail || response.statusText);
    }
  } catch (error) {
    console.error("   :", error);
    console.log({
      word: start_word,
      lemmatize_type: mode,
      result: result,
    });
  }
};


// authService.js

const API_BASE_URL = "https://lemmaapp.ru/server1";

//   
export const handleLogin = async (login, password, setIsLoggedIn, closeModal) => {
  try {
    const response = await fetch(`${API_BASE_URL}/login`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ login, password }),
    });

    const data = await response.json();

    if (response.ok) {
      localStorage.setItem("token", data.access_token);
      setIsLoggedIn(true);
      closeModal();
    } else {
      alert(data.detail || " ");
    }
  } catch (error) {
    console.error(" :", error);
  }
};

//   
export const handleRegister = async (name, surname, login, password, setModalMode) => {
  try {
    const response = await fetch(`${API_BASE_URL}/register`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name, surname, login, password }),
    });

    const data = await response.json();

    if (response.ok) {
      alert(" !    .");
      setModalMode("login");
    } else {
      alert(data.detail || " ");
    }
  } catch (error) {
    console.error(" :", error);
  }
};

//   
export const fetchHistory = async (setHistory, setIsLoggedIn) => {
  try {
    const token = localStorage.getItem("token");
    console.log(token);
    if (!token) {
      alert(" ,   user_id");
      return;
    }

    const response = await fetch(`${API_BASE_URL}/history`, {
      headers: { Authorization: `Bearer ${token}` },
    });

    const data = await response.json();

    if (response.ok) {
      setHistory(data);
    } else {
      alert("  ");
    }
  } catch (error) {
    console.error("  :", error);
  }
};

//   
export const handleLogout = (setIsLoggedIn, setHistory) => {
  localStorage.removeItem("token");
  setIsLoggedIn(false);
  setHistory([]);
};
